import VueRouter from "vue-router";

// 
import routes from "./routes";

// Vuex store
// token
import store from "@/store";

// pushreplace
// pushreplace
const originpush = VueRouter.prototype.push;
const originreplace = VueRouter.prototype.replace;

// push
// location:
VueRouter.prototype.push = function (location, onComplete, onAbort) {
    if (onComplete && onAbort) {
        originpush.call(this, location, onComplete, onAbort);
    } else {
        originpush.call(
            this,
            location,
            () => {},
            () => {}
        );
    }
};
// replace
VueRouter.prototype.replace = function (location, onComplete, onAbort) {
    if (onComplete && onAbort) {
        originreplace.call(this, location, onComplete, onAbort);
    } else {
        originreplace.call(
            this,
            location,
            () => {},
            () => {}
        );
    }
};

// 
let router = new VueRouter({
    // 
    routes,
    /* 
     */
    scrollBehavior(to, from, savedPosition) {
        // 
        // xy
        // return { x : 0 ,y : 0 }
        return { y: 0 };
    },
});

// 
router.beforeEach((to, from, next) => {
    // 1. token
    let token = store.state.user.token; // token()
    let name = store.state.user.userInfo.name; //  tokenAjax
    if (token) {
        // token
        if (to.path == "/login") {
            // home
            next({ name: "home" });
            // console.log("home");
        } else {
            //  login  home|search|detail|shopcart|
            if (name) {
                // tokenactionHeader
                next();
                // console.log("name");
            } else {
                // token
                store
                    .dispatch("user/getUserInfoByToken")
                    .then(() => {
                        // ,
                        next();
                        // console.log("nameaction");
                    })
                    .catch(() => {
                        //  token
                        // tokentoken
                        store.dispatch("user/userLogout");
                        next({ name: "login" });
                        // console.log("token");
                    });
            }
        }
    } else {
        // 
        let topath = to.path;
        // token
        //  |||
        if (
            topath.indexOf("/trade") !== -1 ||
            topath.indexOf("/pay") !== -1 ||
            topath.indexOf("/paysuccess") !== -1 ||
            topath.indexOf("/center") !== -1
        ) {
            // ,
            alert("");
            // next({
            //     name: "login",
            // });

            // path
            // next("/login")
            // query  redirect:"/center/myorder" 
            next(`/login?redirect=${topath}`)
        } else {
            // 
            next();
        }
    }
});

export default router;
