//detailVuex

//detailAjax | 
import { reqGoodsInfo, reqAddorUpdateShopcart } from "@/api/index";

// id
import { getUUID } from "@/utils/uuid_token";

const actions = {
    // 
    async getGoodsInfo(context, skuId) {
        let results = await reqGoodsInfo(skuId);
        // console.log(results);
        if (results.code) {
            context.commit("GETGOODSINFO", results.data);
        }
    },
    //  | ,   
    async getAddorUpdateShopcartInfo(context, { skuId, skuNum }) {
        // console.log(skuId, skuNum);
        let results = await reqAddorUpdateShopcart(skuId, skuNum);
        // console.log(results);  //results.code200
        if (results.code == 200) {
            return "ok";
        } else {
            return Promise.reject(new Error(""));
        }
    },
};
const mutations = {
    GETGOODSINFO(state, goodsInfo) {
        state.goodsInfo = goodsInfo;
        // console.log(goodsInfo);
    },
};
const state = {
    // ()
    goodsInfo: {},
    // id
    uuid_token: getUUID(),
};
// 
const getters = {
    // 
    categoryView() {
        // AjaxgoodsInfocategoryViewundefined
        return state.goodsInfo.categoryView || {};
    },
    // 
    skuInfo() {
        return state.goodsInfo.skuInfo || {};
    },
    // 
    spuSaleAttrList() {
        // 
        return state.goodsInfo.spuSaleAttrList || [];
    },
};

export default {
    namespaced: true,
    actions,
    mutations,
    state,
    getters,
};
