import Vue from "vue" 
//  vee-valadite 
import VeeValidate from "vee-validate";
import zh_CN from "vee-validate/dist/locale/zh_CN"; //  message
Vue.use(VeeValidate)  //

VeeValidate.Validator.localize("zh_CN", {
    messages: {
        ...zh_CN.messages,
        is: (field) => `${field}`, //  message
    },
    attributes: {
        //  field 
        phone: "",
        code: "",
        password: "",
        password1: "",
        agree: "", //agree
    },
});

// const success = await this.$validator.validateAll(); //

//
//
// "tongyi"  agree
VeeValidate.Validator.extend("tongyi", {
    validate: (value) => {
        return value;
    },
    getMessage: (field) => field + "",
});
