<template>
    <div class="register-container">
        <!--  -->
        <div class="register">
            <h3>
                
                <span class="go"> <a href="login.html" target="_blank"></a> </span>
            </h3>
            <!--  -->
            <div class="content">
                <label>:</label>
                <input
                    type="text"
                    placeholder=""
                    v-model="phone"
                    name="phone"
                    v-validate="{ required: true, regex: /^1\d{10}$/ }"
                    :class="{ invalid: errors.has('phone') }"
                />
                <!-- <input type="text" placeholder="" v-model="phone" /> -->
                <span class="error-msg">{{ errors.first("phone") }}</span>
            </div>
            <!--  -->
            <div class="content">
                <label>:</label>
                <input
                    type="text"
                    placeholder=""
                    v-model="code"
                    name="code"
                    v-validate="{ required: true, regex: /^\d{6}$/ }"
                    :class="{ invalid: errors.has('code') }"
                />
                <!-- <input type="text" placeholder="" v-model="code" /> -->
                <button style="width: 100px; height: 38px; margin-left: 20px" @click="getCode">
                    
                </button>

                <span class="error-msg">{{ errors.first("code") }}</span>
            </div>
            <!--  -->
            <div class="content">
                <label>:</label>
                <input
                    type="password"
                    placeholder=""
                    v-model="password"
                    name="password"
                    v-validate="{ required: true, regex: /^[0-9a-zA-Z]{6,15}$/ }"
                    :class="{ invalid: errors.has('password') }"
                />
                <!-- <input type="password" placeholder="" v-model="password" /> -->
                <span class="error-msg">{{ errors.first("password") }}</span>
            </div>
            <!--  -->
            <div class="content">
                <label>:</label>
                <input
                    type="password"
                    placeholder=""
                    v-model="password1"
                    name="password1"
                    v-validate="{ required: true, is: password }"
                    :class="{ invalid: errors.has('password1') }"
                />
                <!-- <input type="password" placeholder="" v-model="password1" /> -->
                <span class="error-msg">{{ errors.first("password1") }}</span>
            </div>
            <!--  -->
            <div class="controls">
                <input
                    type="checkbox"
                    :checked="isAgree"
                    name="agree"
                    v-validate="{ required: true, tongyi: isAgree }"
                    :class="{ invalid: errors.has('agree') }"
                />
                <!-- tongyi  name="agree" agree -->
                <!-- <input name="m1" type="checkbox" :checked="isAgree" /> -->
                <span></span>
                <!-- name  agree -->
                <span class="error-msg">{{ errors.first("agree") }}</span>
            </div>
            <div class="btn">
                <button @click="regUser"></button>
            </div>
        </div>

        <!--  -->
        <div class="copyright">
            <ul>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
            </ul>
            <div class="address">6</div>
            <div class="beian">ICP19006430</div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Register",
    data() {
        return {
            // 
            phone: "",
            // 
            code: "",
            // 
            password: "",
            // 
            password1: "",
            // 
            isAgree: true,
        };
    },
    methods: {
        // 
        getCode() {
            let { phone } = this;
            // phoneaction
            phone &&
                this.$store
                    .dispatch("user/getCode", phone)
                    .then(() => {
                        // 
                        this.code = this.$store.state.user.code;
                    })
                    .catch((error) => {
                        alert(error.message);
                    });
        },
        // 
        async regUser() {
            let { phone, password, code, isAgree, password1 } = this;
            const success = await this.$validator.validateAll(); //
            // console.log(success); //true false
            // phone  code, passwordpassword1 ,isAgreeaction
            if (success) {
                phone &&  password == password1 && isAgree &&
                    this.$store
                        .dispatch("user/regUser", { phone, password, code })
                        .then(() => {
                            // 
                            this.$router.push({
                                name: "login",
                            });
                        })
                        .catch((error) => {
                            alert(error.message);
                        });
            }
        },
    },
};
</script>

<style lang="less" scoped>
.register-container {
    .register {
        width: 1200px;
        height: 445px;
        border: 1px solid rgb(223, 223, 223);
        margin: 0 auto;

        h3 {
            background: #ececec;
            margin: 0;
            padding: 6px 15px;
            color: #333;
            border-bottom: 1px solid #dfdfdf;
            font-size: 20.04px;
            line-height: 30.06px;

            span {
                font-size: 14px;
                float: right;

                a {
                    color: #e1251b;
                }
            }
        }

        div:nth-of-type(1) {
            margin-top: 40px;
        }

        .content {
            padding-left: 390px;
            margin-bottom: 18px;
            position: relative;

            label {
                font-size: 14px;
                width: 96px;
                text-align: right;
                display: inline-block;
            }

            input {
                width: 270px;
                height: 38px;
                padding-left: 8px;
                box-sizing: border-box;
                margin-left: 5px;
                outline: none;
                border: 1px solid #999;
            }

            img {
                vertical-align: sub;
            }

            .error-msg {
                position: absolute;
                top: 100%;
                left: 495px;
                color: red;
            }
        }

        .controls {
            text-align: center;
            position: relative;

            input {
                vertical-align: middle;
            }

            .error-msg {
                position: absolute;
                top: 100%;
                left: 495px;
                color: red;
            }
        }

        .btn {
            text-align: center;
            line-height: 36px;
            margin: 17px 0 0 55px;

            button {
                outline: none;
                width: 270px;
                height: 36px;
                background: #e1251b;
                color: #fff !important;
                display: inline-block;
                font-size: 16px;
            }
        }
    }

    .copyright {
        width: 1200px;
        margin: 0 auto;
        text-align: center;
        line-height: 24px;

        ul {
            li {
                display: inline-block;
                border-right: 1px solid #e4e4e4;
                padding: 0 20px;
                margin: 15px 0;
            }
        }
    }
}
</style>
