// trade 

//Ajax|Ajax
import { reqUserAddressList , reqOrderTradeInfo } from "@/api/index";

const actions = {
    // 
    async getUserAddressList(context){
        let results = await reqUserAddressList()
        if (results.code==200) {
            context.commit("GETUSERADDRESSLIST",results.data)
        }
    },
    //
    async getOrderTradeInfo(context){
        let results = await reqOrderTradeInfo()
        if (results.code==200) {
            context.commit("GETORDERTRADEINFO",results.data)
        }
    }
}
const mutations = {
     // 
    GETUSERADDRESSLIST(state,userAddressList){
        state.userAddressList = userAddressList
    },
    //
    GETORDERTRADEINFO(state,orderInfo){
        state.orderInfo = orderInfo
    }
}
const state = {
    // 
    userAddressList:[],
    // 
    orderInfo:{},
}
const getters = {}

export default {
    namespaced: true,
    actions,
    mutations,
    state,
    getters,
}