import Home from "@/pages/Home";
import Search from "@/pages/Search";
import Login from "@/pages/Login";
import Register from "@/pages/Register";
import Detail from "@/pages/Detail";
import AddCartSuccess from "@/pages/AddCartSuccess";
import ShopCart from "@/pages/ShopCart";
import Trade from "@/pages/Trade";
import Pay from "@/pages/Pay";
import PaySuccess from "@/pages/PaySuccess";
import Center from "@/pages/Center";
import MyOrder from "@/pages/Center/MyOrder";
/* 

JavaScript 

 GroupOrder 

import GroupOrder from "@/pages/Center/GroupOrder";

const GroupOrder = () => import("@/pages/Center/GroupOrder"); // return

const router = createRouter({
  // ...
  routes: [{ path: 'xxxx', component: GroupOrder }],
})
 */

export default [
    {
        name: "home",
        path: "/home",
        component: Home,
        // 
        meta: { showFooter: true },
    },
    {
        name: "detail",
        path: "/detail/:skuId?", //skuIdid
        component: Detail,
        // ,showFooter
        meta: { showFooter: true },
    },
    {
        name: "search",
        // paramsnamepath
        // params?
        path: "/search/:keyword?",
        component: Search,
        meta: { showFooter: true },
        // propsprops
        // a,b
        // props:{a:"1",b:2}

        // params
        // props:true

        // $route
        // props($route) {
        //     return {
        //         keyword: $route.params.keyword,
        //     };
        // },
    },
    {
        // 
        name: "addcartsuccess",
        path: "/addcartsuccess",
        component: AddCartSuccess,
        meta: { showFooter: true },
    },
    {
        // 
        name: "shopcart",
        path: "/shopcart",
        component: ShopCart,
        meta: { showFooter: true },
    },
    {
        // 
        name: "trade",
        path: "/trade",
        component: Trade,
        meta: { showFooter: true },
        // 
        beforeEnter: (to, from, next) => {
            //  
            if (from.path == "/shopcart") {
                // 
                next();
            } else {
                next(false); //url
            }
        },
    },
    {
        // 
        name: "pay",
        path: "/pay",
        component: Pay,
        meta: { showFooter: true },
        // 
        beforeEnter: (to, from, next) => {
            //  
            if (from.path == "/trade") {
                // 
                next();
            } else {
                next(false); //url
            }
        },
    },
    {
        // 
        name: "paySuccess",
        path: "/paySuccess",
        component: PaySuccess,
        meta: { showFooter: true },
        //  
        //  
        // beforeEnter: (to, from, next) => {
        //     //  
        //     if (from.path == "/pay") {
        //         // 
        //         next();
        //     } else {
        //         next(false); //url
        //     }
        // },
        //
    },
    {
        // 
        name: "center",
        path: "/center",
        component: Center,
        meta: { showFooter: true },
        children: [
            {
                // 
                path: "myorder",
                name: "my_order",
                component: MyOrder,
            },
            {
                // 
                path: "grouporder",
                name: "group_order",
                // component: GroupOrder, // 
                // component: GroupOrder, //  GroupOrder
                component: () => import("@/pages/Center/GroupOrder"), //  
            },
            {
                // 
                path: "/center",
                redirect: "/center/myorder",
            },
        ],
    },
    {
        name: "login",
        path: "/login",
        component: Login,
        meta: { showFooter: false },
    },
    {
        name: "register",
        path: "/register",
        component: Register,
        meta: { showFooter: false },
    },
    {
        // / home
        path: "*",
        redirect: "/home",
    },
];
