<template>
    <!--  -->
    <div class="order-content">
        <div class="title">
            <h3></h3>
        </div>
        <!--  -->
        <div class="chosetype">
            <table>
                <thead>
                    <tr>
                        <th width="29%"></th>
                        <th width="31%"></th>
                        <th width="13%"></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
            </table>
        </div>
        <!--  -->
        <div class="orders">
            <!--  -->
            <table class="order-item" v-for="order in myOrderList.records" :key="order.id">
                <!--  -->
                <thead>
                    <tr>
                        <th colspan="5">
                            <span class="ordertitle"
                                >{{ order.createTime }} {{ order.outTradeNo }}
                                <span class="pull-right delete"
                                    ><img src="../images/delete.png" /></span
                            ></span>
                        </th>
                    </tr>
                </thead>

                <tbody>
                    <!--  -->
                    <tr v-for="(cart, index) in order.orderDetailList" :key="cart.id">
                        <td width="60%">
                            <div class="typographic">
                                <img :src="cart.imgUrl" style="wight: 82px; height: 82px" />
                                <a href="#" class="block-text">{{ cart.skuName }}</a>
                                <span>x{{ cart.skuNum }}</span>
                                <a href="#" class="service"></a>
                            </div>
                        </td>
                        <!--  -->
                        <!-- || -->
                        <td
                            :rowspan="order.orderDetailList.length"
                            width="8%"
                            class="center"
                            v-if="index == 0"
                        >
                            {{ order.consignee }}
                        </td>
                        <td
                            :rowspan="order.orderDetailList.length"
                            width="13%"
                            class="center"
                            v-if="index == 0"
                        >
                            <ul class="unstyled">
                                <li>{{ order.totalAmount }}.00</li>
                                <li></li>
                            </ul>
                        </td>
                        <td
                            :rowspan="order.orderDetailList.length"
                            width="8%"
                            class="center"
                            v-if="index == 0"
                        >
                            <a href="#" class="btn">{{ order.orderStatusName }} </a>
                        </td>
                        <td
                            :rowspan="order.orderDetailList.length"
                            width="13%"
                            class="center"
                            v-if="index == 0"
                        >
                            <ul class="unstyled">
                                <li>
                                    <a href="mycomment.html" target="_blank">|</a>
                                </li>
                            </ul>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <!-- :  Pagination -->
        <!-- pageNo: | pageSize | continuousPage | 
        totalData | getPageNo() -->
        <Pagination
            :pageNo="page"
            :pageSize="limit"
            :continuousPage="5"
            :totalData="myOrderList.total"
            @getPageNo="getPageNo"
        />
    </div>
</template>

<script>
export default {
    name: "MyOrder",
    data() {
        return {
            page: "1", //
            limit: "3", //
            // |
            myOrderList: {},
        };
    },
    mounted() {
        // 
        // 
        this.getMyOrderList();
    },
    methods: {
        //   
        async getMyOrderList() {
            // 
            let { page, limit } = this;
            let results = await this.$API.reqMyOrderList(page, limit);
            if (results.code == 200) {
                //   
                this.myOrderList = results.data;
            }
        },
        //  getPageNo getPageNo +
        getPageNo(page){
            this.page = page
            this.getMyOrderList();
        }
    },
};
</script>
