<template>
    <div class="pagination">
        <!-- 1 -->
        <!-- getPageNo, -->
        <button :disabled="pageNo == 1" @click="$emit('getPageNo', pageNo - 1)"></button>
        <!-- 1 -->
        <!-- 1 -->
        <button v-if="startAndEndNumber.startNum > 1" @click="$emit('getPageNo', 1)">1</button>
        <!-- 2 -->
        <button v-if="startAndEndNumber.startNum > 2"></button>

        <!-- v-for  ||| -->
        <!--  -->
        <!-- pageNopage -->
        <button
            v-for="(page, index) in startAndEndNumber.endNum"
            :key="index"
            v-show="page >= startAndEndNumber.startNum"
            @click="$emit('getPageNo', page)"
            :class="{ showBlue: pageNo == page }"
        >
            {{ page }}
        </button>

        <!-- 1 -->
        <button v-if="totalPage - startAndEndNumber.endNum > 1"></button>
        <!--   -->
        <button v-if="startAndEndNumber.endNum < totalPage" @click="$emit('getPageNo', totalPage)">
            {{ totalPage }}
        </button>
        <!--  -->
        <button :disabled="pageNo == totalPage" @click="$emit('getPageNo', pageNo + 1)">
            
        </button>
        <button style="margin-left: 30px"> {{ totalData }} </button>
    </div>
</template>

<script>
export default {
    // 
    name: "Pagination",
    props: ["pageNo", "pageSize", "continuousPage", "totalData"],
    computed: {
        // 
        totalPage() {
            // 
            return Math.ceil(this.totalData / this.pageSize);
        },
        // 
        startAndEndNumber() {
            // 
            let startNum = 0;
            let endNum = 0;
            // 
            if (this.continuousPage > this.totalPage) {
                startNum = 1;
                endNum = this.totalPage;
            } else {
                // 
                startNum = this.pageNo - parseInt(this.continuousPage / 2);
                endNum = this.pageNo + parseInt(this.continuousPage / 2);
                // bugpageNo21startNum0-1 startNum1
                if (startNum < 1) {
                    startNum = 1;
                    endNum = this.continuousPage;
                }
                // bug,pageNoendNumendNumtotalPage
                if (endNum > this.totalPage) {
                    endNum = this.totalPage;
                    startNum = this.totalPage - this.continuousPage + 1;
                }
            }
            return { startNum, endNum };
        },
    },
};
</script>

<style lang="less" scoped>
.pagination {
    text-align: center;
    .showBlue {
        background: skyblue;
    }
    button {
        margin: 0 5px;
        background-color: #f4f4f5;
        color: #606266;
        outline: none;
        border-radius: 2px;
        padding: 0 4px;
        vertical-align: top;
        display: inline-block;
        font-size: 13px;
        min-width: 35.5px;
        height: 28px;
        line-height: 28px;
        cursor: pointer;
        box-sizing: border-box;
        text-align: center;
        border: 0;

        &[disabled] {
            color: #c0c4cc;
            cursor: not-allowed;
        }

        &.active {
            cursor: not-allowed;
            background-color: #409eff;
            color: #fff;
        }
    }
}
</style>
