// id
// uuidas Vue2uuid id
import { v4 as uuidv4 } from "uuid";
export function getUUID() {
    // UUIDTOKEN
    let uuid_token = localStorage.getItem("UUIDTOKEN");
    // UUIDTOKEN
    if (!uuid_token) {
        // 
        uuid_token = uuidv4();
        // 
        localStorage.setItem("UUIDTOKEN", uuid_token);
    }
    // uuid_token
    return uuid_token;
}
