// search
import { reqSearchInfo } from "@/api/index";

const actions = {
    // searchactions
    async getSearchList(context, params) {
        let results = await reqSearchInfo(params);
        if (results.code == 200) {
            context.commit("GETSEARCHLIST", results.data);
        }
        else if(err){
            console.log(err.message);
        }
        // else {
        //     console.log(results);
        // }
    },
};
const mutations = {
    GETSEARCHLIST(state, searchList) {
        state.searchList = searchList;
    },
};
const state = {
    // searchList
    searchList: {},
};
const getters = {
    // getterssearchList
    //  goodsList
    goodsList(state) {
        // state.searchList.goodsList undefined,,
        return state.searchList.goodsList || [];
    },
    // 
    attrsList(state) {
        return state.searchList.attrsList || [];
    },
    // 
    trademarkList(state) {
        return state.searchList.trademarkList || [];
    },
    // 
    totalData(state) {
        return state.searchList.total;
    },
};

export default {
    namespaced: true,
    actions,
    mutations,
    state,
    getters,
};
