<template>
    <!-- HomeSearchDetail -->
    <!--  -->
    <div class="type-nav">
        <div class="container">
            <div @mouseleave="leaveIndex" @mouseenter="changeShow">
                <h2 class="all"></h2>
                <!--  -->
                <!--  <transition 
                    name="animate__animated animate__bounce"
                    enter-active-class="animate__fadeIn" 
                    leave-active-class="animate__fadeOutBottomLeft"
                > -->
                <transition name="sort">
                    <!--  -->
                    <div class="sort" v-show="Show">
                        <div class="all-sort-list2" @click="goSearch">
                            <!--  -->
                            <div
                                class="item bo"
                                v-for="(c1, index) in CategoryList"
                                :key="c1.categoryId"
                                @mouseenter="changeIndex(index)"
                                :class="{ cur: index === currentIndex }"
                            >
                                <h3>
                                    <!-- 
                                     <router-link ></router-link> 
                                    1000+router-link-->
                                    <!-- <router-link :to="{
                                        name : 'search',
                                        query:{
                                            categoryName:c1.categoryName,
                                            categoryId:c1.categoryId
                                        }
                                    }">{{ c1.categoryName }}</router-link> -->

                                    <!-- 
                                    
                                    1000+goSearch 
                                    ahref-->
                                    <!-- <a @click="goSearch">{{ c1.categoryName }}</a> -->

                                    <!-- 
                                    :
                                    
                                    1.aa
                                    2.a a
                                    1:a data-categoryName a
                                    2 a 
                                    data-category1Id , data-category2Id , data-category3Id -->

                                    <a
                                        :data-categoryName="c1.categoryName"
                                        :data-category1Id="c1.categoryId"
                                        >{{ c1.categoryName }}</a
                                    >
                                </h3>
                                <!--  -->
                                <div
                                    class="item-list clearfix"
                                    :style="{ display: index == currentIndex ? 'block' : 'none' }"
                                >
                                    <div
                                        class="subitem"
                                        v-for="c2 in c1.categoryChild"
                                        :key="c2.categoryId"
                                    >
                                        <dl class="fore">
                                            <dt>
                                                <a
                                                    :data-categoryName="c2.categoryName"
                                                    :data-category2Id="c2.categoryId"
                                                    >{{ c2.categoryName }}</a
                                                >
                                            </dt>
                                            <dd>
                                                <em
                                                    v-for="c3 in c2.categoryChild"
                                                    :key="c3.categoryId"
                                                >
                                                    <a
                                                        :data-categoryName="c3.categoryName"
                                                        :data-category3Id="c3.categoryId"
                                                        >{{ c3.categoryName }}</a
                                                    >
                                                </em>
                                            </dd>
                                        </dl>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </transition>
            </div>
            <nav class="nav">
                <a href="###"></a>
                <a href="###"></a>
                <a href="###"></a>
                <a href="###"></a>
                <a href="###"></a>
                <a href="###"></a>
                <a href="###"></a>
                <a href="###"></a>
            </nav>
        </div>
    </div>
</template>

<script>
//  lodash ()
// import _ from "lodash"
//  lodash  throttle  
import throttle from "lodash/throttle";

// animate.css
import "animate.css";

export default {
    name: "TypeNav",
    data() {
        return {
            currentIndex: -1,
            Show: true,
        };
    },
    methods: {
        changeIndex: throttle(function (index) {
            this.currentIndex = index;
        }, 70),
        leaveIndex() {
            this.currentIndex = -1;
            // homeshowfalse
            if (this.$route.path !== "/home") {
                this.Show = false;
            }
        },
        // asearch
        goSearch(event) {
            // console.log(event.target.dataset);
            // event.target.dataset   //categoryname dataset data- 
            let { categoryname, category1id, category2id, category3id } = event.target.dataset; //
            // 1: categorynamea
            if (categoryname) {
                // 
                let location = {
                    name: "search",
                    query:{
                        categoryName: categoryname,
                    }
                };
                // 2
                if (category1id) {
                    // a
                    location.query.category1Id = category1id;
                } else if (category2id) {
                    // a
                    location.query.category2Id = category2id;
                } else {
                    // a
                    location.query.category3Id = category3id;
                }
                if (this.$route.params.keyword) {
                    location.params = this.$route.params;
                }
                this.$router.push(location);
            }
        },
        // 
        changeShow() {
            this.Show = true;
        },
    },

    mounted() {
        /* TypeNav
          Show v-show */
        if (this.$route.path !== "/home") {
            this.Show = false;
        }
    },
    computed: {
        CategoryList() {
            return this.$store.state.home.CategoryList;
        },
    },
};
</script>

<style lang="less" scoped>
.type-nav {
    border-bottom: 2px solid #e1251b;

    .container {
        width: 1200px;
        margin: 0 auto;
        display: flex;
        position: relative;

        .all {
            width: 210px;
            height: 45px;
            background-color: #e1251b;
            line-height: 45px;
            text-align: center;
            color: #fff;
            font-size: 14px;
            font-weight: bold;
        }

        .nav {
            a {
                height: 45px;
                margin: 0 22px;
                line-height: 45px;
                font-size: 16px;
                color: #333;
            }
        }

        .sort {
            position: absolute;
            left: 0;
            top: 45px;
            width: 210px;
            height: 461px;
            position: absolute;
            background: #fafafa;
            z-index: 999;

            .all-sort-list2 {
                .item {
                    h3 {
                        line-height: 28px;
                        font-size: 14px;
                        font-weight: 400;
                        overflow: hidden;
                        padding: 0 20px;
                        margin: 0;

                        a {
                            color: #333;
                        }
                    }

                    .item-list {
                        display: none;
                        position: absolute;
                        width: 734px;
                        min-height: 460px;
                        background: #f7f7f7;
                        left: 210px;
                        border: 1px solid #ddd;
                        top: 0;
                        z-index: 9999 !important;

                        .subitem {
                            float: left;
                            width: 650px;
                            padding: 0 4px 0 8px;

                            dl {
                                border-top: 1px solid #eee;
                                padding: 6px 0;
                                overflow: hidden;
                                zoom: 1;

                                &.fore {
                                    border-top: 0;
                                }

                                dt {
                                    float: left;
                                    width: 54px;
                                    line-height: 22px;
                                    text-align: right;
                                    padding: 3px 6px 0 0;
                                    font-weight: 700;
                                }

                                dd {
                                    float: left;
                                    width: 415px;
                                    padding: 3px 0 0;
                                    overflow: hidden;

                                    em {
                                        float: left;
                                        height: 14px;
                                        line-height: 14px;
                                        padding: 0 8px;
                                        margin-top: 5px;
                                        border-left: 1px solid #ccc;
                                    }
                                }
                            }
                        }
                    }
                    //less &   .item:hover {...}
                    // &:hover {
                    //     .item-list {
                    //         display: block;
                    //     }
                    // }
                }
                /* index===currentIndextrue
                 :class="{cur:index===currentIndex}" */
                .cur {
                    background-color: skyblue;
                }
                /* 
                .item:hover{
                    background-color: skyblue;
                } */
            }
        }
        /*: */
        .sort-enter {
            height: 0px;
        }
        .sort-enter-active {
            transition: all 0.5s;
        }
        .sort-enter-to {
            height: 461px;
        }
    }
}
</style>
