<template>
    <div class="cart">
        <h4></h4>
        <div class="cart-main">
            <div class="cart-th">
                <div class="cart-th1"></div>
                <div class="cart-th2"></div>
                <div class="cart-th3"></div>
                <div class="cart-th4"></div>
                <div class="cart-th5"></div>
                <div class="cart-th6"></div>
            </div>
            <div class="cart-body">
                <!-- shopInfo:id,url -->
                <ul class="cart-list" v-for="shopInfo in cartInfoList" :key="shopInfo.id">
                    <li class="cart-list-con1">
                        <!-- isChecked1 -->
                        <input
                            type="checkbox"
                            name="chk_list"
                            :checked="shopInfo.isChecked"
                            @change="changeShopcartChecked($event, shopInfo)"
                        />
                    </li>
                    <li class="cart-list-con2">
                        <img :src="shopInfo.imgUrl" />
                        <div class="item-msg">
                            {{ shopInfo.skuName }}
                        </div>
                    </li>
                    <li class="cart-list-con4">
                        <span class="price">{{ shopInfo.skuPrice }}.00</span>
                    </li>
                    <li class="cart-list-con5">
                        <!--  -->
                        <a
                            href="javascript:void(0)"
                            class="mins"
                            @click="handler('sub', -1, shopInfo)"
                            >-</a
                        >
                        <input
                            autocomplete="off"
                            type="text"
                            :value="shopInfo.skuNum"
                            minnum="1"
                            class="itxt"
                            @change="handler('change', $event.target.value * 1, shopInfo)"
                        />
                        <a
                            href="javascript:void(0)"
                            class="plus"
                            @click="handler('add', 1, shopInfo)"
                            >+</a
                        >
                    </li>
                    <li class="cart-list-con6">
                        <span class="sum">{{ shopInfo.skuNum * shopInfo.skuPrice }}.00</span>
                    </li>
                    <li class="cart-list-con7">
                        <a class="sindelet" @click="DeleteShopcartById(shopInfo)"></a>
                        <br />
                        <a href="#none"></a>
                    </li>
                </ul>
            </div>
        </div>
        <div class="cart-tool">
            <div class="select-all">
                <input
                    class="chooseAll"
                    type="checkbox"
                    :checked="isAllChecked"
                    @change="checkedAll"
                />
                <span></span>
            </div>
            <div class="option">
                <a @click="deleteAllShopCartChecked"></a>
                <a href="#none"></a>
                <a href="#none"></a>
            </div>
            <div class="money-box">
                <div class="chosed"> <span>0</span></div>
                <div class="sumprice">
                    <em> </em>
                    <i class="summoney">{{ totalPrices }}</i>
                </div>
                <div class="sumbtn">
                    <router-link class="sum-btn" to="/trade"></router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
// lodash
import throttle from "lodash/throttle";
export default {
    name: "ShopCart",
    // 
    mounted() {
        this.getData();
    },
    methods: {
        // 
        getData() {
            this.$store.dispatch("shopcart/getShopcartList");
        },
        // 
        // type:
        /* disNum: -1  1   input ()
        skuNum */
        // shopInfo:idskuId
        handler: throttle(async function (type, disNum, shopInfo) {
            //  switch type break
            switch (type) {
                // type"add",disNum1
                case "add":
                    disNum = 1;
                    break;
                case "sub":
                    // 1
                    // if (shopInfo.skuNum > 1) {
                    //     disNum = -1;
                    // } else {
                    //     // 1disNum0()
                    //     disNum = 0;
                    // }
                    // :
                    disNum = shopInfo.skuNum > 1 ? -1 : 0;
                    break;
                case "change":
                    //|1disNum0()
                    if (isNaN(disNum) || disNum < 1) {
                        disNum = 0;
                    } else {
                        // 
                        disNum = parseInt(disNum) - shopInfo.skuNum;
                    }
                    // 
                    // disNum = (isNaN(disNum) || disNum < 1) ? 0 : (parseInt(disNum) - shopInfo.skuNum)
                    break;
            }
            // action,
            /* this.$store.dispatch("detail/getAddorUpdateShopcartInfo",{ skuId:shopInfo.skuId, skuNum:disNum })
             // thencatch
            // 
            .then(()=>{
                // 
                this.getData()
            })
            // 
            .catch((error)=>{
                alert(error.message)
            }) */
            // trycatchasync
            try {
                // 
                await this.$store.dispatch("detail/getAddorUpdateShopcartInfo", {
                    skuId: shopInfo.skuId,
                    skuNum: disNum,
                });
                // ,
                this.getData();
            } catch (error) {
                alert(error.message);
            }
        }, 500),
        // 
        DeleteShopcartById(shopInfo) {
            this.$store
                .dispatch("shopcart/DeleteShopcartById", shopInfo.skuId)
                .then(() => {
                    // ,
                    this.getData();
                })
                .catch((error) => {
                    // 
                    alert(error.message);
                });
        },
        // 
        changeShopcartChecked($event, shopInfo) {
            let checked = $event.target.checked;
            // console.log(shopInfo.skuId,checked);
            // checked1isCheckedchecked0isChecked
            let isChecked = checked ? 1 : 0;
            // console.log(checked);
            this.$store
                .dispatch("shopcart/changeShopcartChecked", { skuId: shopInfo.skuId, isChecked })
                .then(() => {
                    // 
                    this.getData();
                    // console.log("");
                })
                .catch((error) => {
                    alert(error.message);
                });
        },
        //   isChecked = 1  isChecked = 0
        checkedAll($event) {
            if ($event.target.checked) {
                // 
                this.cartInfoList.forEach((element) => {
                    this.changeShopcartChecked($event, element);
                });
                //  console.log("");
            } else {
                // 
                this.cartInfoList.forEach((element) => {
                    this.changeShopcartChecked($event, element);
                });
                // console.log("");
            }
        },
        // 
        deleteAllShopCartChecked() {
            this.$store
                .dispatch("shopcart/deleteAllShopCartChecked", this.cartInfoList)
                // promise
                .then(() => {
                    // 
                    this.getData();
                })
                // promise
                .catch((error) => {
                    // 
                    alert(error.message);
                });
        },
    },
    computed: {
        // cartInfoObj
        ...mapGetters("shopcart", ["cartInfoObj"]),
        // cartInfoList,
        cartInfoList() {
            return this.cartInfoObj.cartInfoList || [];
        },
        // 
        totalPrices() {
            let sum = 0;
            this.cartInfoList.forEach((element) => {
                sum += element.skuNum * element.skuPrice;
            });
            return sum;
        },
        // 
        isAllChecked() {
            // return
            if (this.cartInfoList.length > 0) {
                return this.cartInfoList.every((item) => item.isChecked == 1);
            }else{
                return false;
            }
        },
    },
};
</script>

<style lang="less" scoped>
.cart {
    width: 1200px;
    margin: 0 auto;

    h4 {
        margin: 9px 0;
        font-size: 14px;
        line-height: 21px;
    }

    .cart-main {
        .cart-th {
            background: #f5f5f5;
            border: 1px solid #ddd;
            padding: 10px;
            overflow: hidden;

            & > div {
                float: left;
            }

            .cart-th1 {
                width: 25%;

                input {
                    vertical-align: middle;
                }

                span {
                    vertical-align: middle;
                }
            }

            .cart-th2 {
                width: 25%;
            }

            .cart-th3,
            .cart-th4,
            .cart-th5,
            .cart-th6 {
                width: 12.5%;
            }
        }

        .cart-body {
            margin: 15px 0;
            border: 1px solid #ddd;

            .cart-list {
                padding: 10px;
                border-bottom: 1px solid #ddd;
                overflow: hidden;

                & > li {
                    float: left;
                }

                .cart-list-con1 {
                    width: 15%;
                }

                .cart-list-con2 {
                    width: 35%;

                    img {
                        width: 82px;
                        height: 82px;
                        float: left;
                    }

                    .item-msg {
                        float: left;
                        width: 150px;
                        margin: 0 10px;
                        line-height: 18px;
                    }
                }

                // .cart-list-con3 {
                //   width: 20.8333%;

                //   .item-txt {
                //     text-align: center;
                //   }
                // }

                .cart-list-con4 {
                    width: 10%;
                }

                .cart-list-con5 {
                    width: 17%;

                    .mins {
                        border: 1px solid #ddd;
                        border-right: 0;
                        float: left;
                        color: #666;
                        width: 6px;
                        text-align: center;
                        padding: 8px;
                    }

                    input {
                        border: 1px solid #ddd;
                        width: 40px;
                        height: 33px;
                        float: left;
                        text-align: center;
                        font-size: 14px;
                    }

                    .plus {
                        border: 1px solid #ddd;
                        border-left: 0;
                        float: left;
                        color: #666;
                        width: 6px;
                        text-align: center;
                        padding: 8px;
                    }
                }

                .cart-list-con6 {
                    width: 10%;

                    .sum {
                        font-size: 16px;
                    }
                }

                .cart-list-con7 {
                    width: 13%;

                    a {
                        color: #666;
                    }
                }
            }
        }
    }

    .cart-tool {
        overflow: hidden;
        border: 1px solid #ddd;

        .select-all {
            padding: 10px;
            overflow: hidden;
            float: left;

            span {
                vertical-align: middle;
            }

            input {
                vertical-align: middle;
            }
        }

        .option {
            padding: 10px;
            overflow: hidden;
            float: left;

            a {
                float: left;
                padding: 0 10px;
                color: #666;
            }
        }

        .money-box {
            float: right;

            .chosed {
                line-height: 26px;
                float: left;
                padding: 0 10px;
            }

            .sumprice {
                width: 200px;
                line-height: 22px;
                float: left;
                padding: 0 10px;

                .summoney {
                    color: #c81623;
                    font-size: 16px;
                }
            }

            .sumbtn {
                float: right;

                a {
                    display: block;
                    position: relative;
                    width: 96px;
                    height: 52px;
                    line-height: 52px;
                    color: #fff;
                    text-align: center;
                    font-size: 18px;
                    font-family: "Microsoft YaHei";
                    background: #e1251b;
                    overflow: hidden;
                }
            }
        }
    }
}
</style>
