// search
// /api/list   post   
/* {
    "category3Id": "61",
    "categoryName": "",
    "keyword": "",
    "order": "1:desc",
    "pageNo": 1,
    "pageSize": 10,
    "props": ["1:1700-2799:", "2:6.65-6.74:"],
    "trademark": "4:"
  } */
//   
export const reqSearchInfo = (params) => {
    return requests({
        url: "/list",
        method: "post",
        data: params,
    });
};

// detail,skuId
// /api/item/{ skuId }  get  skuIdID string
export const reqGoodsInfo = (skuId) => {
    return requests({
        url: `/item/${skuId}`,
        method: "get",
    });
};

