<template>
    <div>
        <TypeNav />
        <!--list-content-->
        <div class="main">
            <div class="py-container">
                <!--bread -->
                <div class="bread">
                    <ul class="fl sui-breadcrumb">
                        <li>
                            <a href="#"></a>
                        </li>
                    </ul>
                    <ul class="fl sui-tag">
                        <!--    query-->
                        <li class="with-x" v-if="searchParams.categoryName">
                            {{ searchParams.categoryName }}<i @click="removeCategoryName"></i>
                        </li>
                        <!--    params-->
                        <li class="with-x" v-if="searchParams.keyword">
                            {{ searchParams.keyword }}<i @click="removeKeyword"></i>
                        </li>
                        <!--   -->
                        <li class="with-x" v-if="searchParams.trademark">
                            <!--  : 1tmName -->
                            {{ searchParams.trademark.split(":")[1]
                            }}<i @click="removeTrademark"></i>
                        </li>
                        <!--   -->
                        <li
                            class="with-x"
                            v-for="(attrValue, index) in searchParams.props"
                            :key="index"
                        >
                            {{ attrValue.split(":")[1] }}<i @click="removeAttrValue(index)"></i>
                        </li>
                    </ul>
                </div>
                <!--selector -->
                <!--  -->
                <SearchSelector @trademarkInfo="trademarkInfo" @attrsInfo="attrsInfo" />

                <!--details -->
                <div class="details clearfix">
                    <div class="sui-navbar">
                        <div class="navbar-inner filter">
                            <ul class="sui-nav">
                                <!--  -->
                                <!--  -->
                                <li :class="{ active: isOne }" @click="changeOrder('1')">
                                    <a
                                        ><span
                                            v-show="isOne"
                                            class="iconfont"
                                            :class="{ 'icon-up': isAsc, 'icon-down': isDesc }"
                                        ></span
                                    ></a>
                                </li>
                                <li :class="{ active: isTwo }" @click="changeOrder('2')">
                                    <a
                                        ><span
                                            v-show="isTwo"
                                            class="iconfont"
                                            :class="{ 'icon-up': isAsc, 'icon-down': isDesc }"
                                        ></span
                                    ></a>
                                </li>
                            </ul>
                        </div>
                    </div>
                    <!-- goods-list  -->
                    <div class="goods-list">
                        <ul class="yui3-g">
                            <li class="yui3-u-1-5" v-for="goods in goodsList" :key="goods.id">
                                <div class="list-wrap">
                                    <div class="p-img">
                                        <!-- searchdetail -->
                                        <router-link
                                            :to="{
                                                name: 'detail',
                                                params: {
                                                    skuId: goods.id,
                                                },
                                            }"
                                        >
                                            <!-- v-lazy vue-lazyload -->
                                            <!-- <img :src="goods.defaultImg" > -->
                                            <img v-lazy="goods.defaultImg" />
                                        </router-link>
                                    </div>
                                    <div class="price">
                                        <strong>
                                            <em></em>
                                            <i>{{ goods.price }}</i>
                                        </strong>
                                    </div>
                                    <div class="attr">
                                        <a
                                            target="_blank"
                                            href="item.html"
                                            title="CIBN4A 58 "
                                            >{{ goods.title }}</a
                                        >
                                    </div>
                                    <div class="commit">
                                        <i class="command"><span>2000</span></i>
                                    </div>
                                    <div class="operate">
                                        <a
                                            href="success-cart.html"
                                            target="_blank"
                                            class="sui-btn btn-bordered btn-danger"
                                            ></a
                                        >
                                        <a href="javascript:void(0);" class="sui-btn btn-bordered"
                                            ></a
                                        >
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <!--  -->
                    <Pagination
                        :pageNo="searchParams.pageNo"
                        :pageSize="searchParams.pageSize"
                        :continuousPage="5"
                        :totalData="totalData"
                        @getPageNo="getPageNo"
                    />
                </div>
                <!--hotsale -->
                <!-- <div class="clearfix hot-sale">
                    <h4 class="title"></h4>
                    <div class="hot-list">
                        <ul class="yui3-g">
                            <li class="yui3-u-1-4">
                                <div class="list-wrap">
                                    <div class="p-img">
                                        <img src="./images/like_01.png" />
                                    </div>
                                    <div class="attr">
                                        <em>AppleiPhone 6s (A1699)</em>
                                    </div>
                                    <div class="price">
                                        <strong>
                                            <em></em>

                                            <i>4088.00</i>
                                        </strong>
                                    </div>
                                    <div class="commit">
                                        <i class="command">700</i>
                                    </div>
                                </div>
                            </li>
                            <li class="yui3-u-1-4">
                                <div class="list-wrap">
                                    <div class="p-img">
                                        <img src="./images/like_03.png" />
                                    </div>
                                    <div class="attr">
                                        <em>A360APP300</em>
                                    </div>
                                    <div class="price">
                                        <strong>
                                            <em></em>
                                            <i>4088.00</i>
                                        </strong>
                                    </div>
                                    <div class="commit">
                                        <i class="command">700</i>
                                    </div>
                                </div>
                            </li>
                            <li class="yui3-u-1-4">
                                <div class="list-wrap">
                                    <div class="p-img">
                                        <img src="./images/like_04.png" />
                                    </div>
                                    <div class="attr">
                                        <em>256SSDAPP200</em>
                                    </div>
                                    <div class="price">
                                        <strong>
                                            <em></em>
                                            <i>4068.00</i>
                                        </strong>
                                    </div>
                                    <div class="commit">
                                        <i class="command">20</i>
                                    </div>
                                </div>
                            </li>
                            <li class="yui3-u-1-4">
                                <div class="list-wrap">
                                    <div class="p-img">
                                        <img src="./images/like_02.png" />
                                    </div>
                                    <div class="attr">
                                        <em>AppleiPhone 6s (A1699)</em>
                                    </div>
                                    <div class="price">
                                        <strong>
                                            <em></em>
                                            <i>4088.00</i>
                                        </strong>
                                    </div>
                                    <div class="commit">
                                        <i class="command">700</i>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div> -->
            </div>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";

// SearchSelector
import SearchSelector from "./SearchSelector";

export default {
    name: "Search",
    // props
    // props: ["keyword"],
    data() {
        return {
            // 
            searchParams: {
                category1Id: "", //id
                category2Id: "", //id
                category3Id: "", //id
                categoryName: "", //
                keyword: "", //
                props: [], //
                trademark: "", //

                /*  
                1: ,2:  asc: ,desc:   
                : "1:desc" 
                  
                indexOforder12  12
                 indexOforderascdesc asc2 */
                order: "1:desc", // :1:desc

                pageNo: 1, //  :1
                pageSize: 2, // :10 goodsList()
            },
        };
    },

    components: {
        // 
        SearchSelector,
    },
    computed: {
        ...mapGetters("search", ["goodsList", "totalData"]),

        // searchParamsorder1indexOf-1 isOnetrue
        isOne() {
            return this.searchParams.order.indexOf(1) !== -1;
        },
        // searchParamsorder2indexOf-1 isTwotrue
        isTwo() {
            return this.searchParams.order.indexOf(2) !== -1;
        },
        // searchParamsorderascindexOf-1 isAsctrue
        isAsc() {
            return this.searchParams.order.indexOf("asc") !== -1;
        },
        // searchParamsorderdescindexOf-1 isDesctrue
        isDesc() {
            return this.searchParams.order.indexOf("desc") !== -1;
        },
    },
    methods: {
        // search
        getData() {
            this.$store.dispatch("search/getSearchList", this.searchParams);
        },
        //  x  categoryName
        removeCategoryName() {
            // undefined"",undefined
            this.searchParams.categoryName = undefined;
            // xcategoryNamecategoryIdundefined
            this.searchParams.category1Id = undefined;
            this.searchParams.category2Id = undefined;
            this.searchParams.category3Id = undefined;
            // 
            this.getData();
            //  categoryName 
            this.$router.push({
                name: "search",
                //paramsHeader
                // query
                params: this.$route.params,
            });
        },
        //  x  keyword
        removeKeyword() {
            this.searchParams.keyword = undefined;
            // 
            this.getData();
            // Header
            this.$bus.$emit("clearKeyword");
            //  keyword 
            this.$router.push({
                name: "search",
                query: this.$route.query,
            });
        },
        //trademarkInfo 
        trademarkInfo(trademark) {
            //
            //   searchParamstrademark "ID:"
            this.searchParams.trademark = `${trademark.tmId}:${trademark.tmName}`;
            // 
            this.getData();
        },
        //  x  trademark
        removeTrademark() {
            this.searchParams.trademark = undefined;
            // 
            this.getData();
        },
        // attrsInfo    props["ID::"]
        attrsInfo(attrs, attrValue) {
            // props 
            let propsStr = `${attrs.attrId}:${attrValue}:${attrs.attrName}`;
            // 
            //  searchParams  propsindexOf-1,props
            if (this.searchParams.props.indexOf(propsStr) == -1) {
                this.searchParams.props.push(propsStr);
            }
            // 
            this.getData();
        },
        //  x  attrValue
        removeAttrValue(index) {
            // attrValueprops
            this.searchParams.props.splice(index, 1);
            // 
            this.getData();
        },
        // 
        changeOrder(flag) {
            // flag 
            // order  
            let originOrderFlag = this.searchParams.order.split(":")[0];
            let originOrderSort = this.searchParams.order.split(":")[1];
            // order
            let newOrderStr = "";
            //  1
            if (flag == originOrderFlag) {
                // 
                newOrderStr = `${flag}:${originOrderSort == "desc" ? "asc" : "desc"}`;
                console.log(newOrderStr);
            } else {
                newOrderStr = `${flag}:${"desc"}`;
                console.log(newOrderStr);
            }
            this.searchParams.order = newOrderStr;
            this.getData();
        },
        //  getPageNo pageNo
        getPageNo(pageNo) {
            // searchParamspageNo
            // 
            this.searchParams.pageNo = pageNo;
            this.getData();
        },
    },
    beforeMount() {
        // homequeryparamssearchParams
        Object.assign(this.searchParams, this.$route.query, this.$route.params);
    },
    mounted() {
        // search
        this.getData();
        // mounted Ajax
    },
    watch: {
        // watch$routedatacomputed
        $route() {
            // Head$route
            // searchParamsAjax
            // searchParams
            Object.assign(this.searchParams, this.$route.query, this.$route.params);
            this.getData();
            // category1Idcategory2Idcategory3Id 
            //  categoryName
            this.searchParams.category1Id = "";
            this.searchParams.category2Id = "";
            this.searchParams.category3Id = "";
        },
    },
};
</script>

