<script>
import ImageList from "./ImageList/ImageList";
import Zoom from "./Zoom/Zoom";
import { mapGetters } from "vuex";
export default {
    name: "Detail",
    data() {
        return {
            // 1,
            skuNum: 1,
        };
    },
    components: {
        ImageList,
        Zoom,
    },
    computed: {
        //  ,  , 
        ...mapGetters("detail", ["categoryView", "skuInfo", "spuSaleAttrList"]),
        skuImageList() {
            // skuInfoskuImageListundefined
            return this.skuInfo.skuImageList || [];
        },
    },
    methods: {
        // spuSaleAttrValuearr
        changeActive(spuSaleAttrValue, arr) {
            // 
            // arr, isChecked "0"
            arr.forEach((element) => {
                element.isChecked = "0";
            });
            // "1",
            spuSaleAttrValue.isChecked = "1";
        },
        // 
        // input
        getSkuNumValue(event) {
            // 
            // 1. 0
            // let value = event.target.value
            // let regExp = /^[1-9][0-9]*$/
            // let ok = regExp.test(value)

            // 2.
            // 1
            let value = event.target.value * 1;
            // console.log(value);
            // valueNaN,isNaN(value)true
            if (isNaN(value) || value < 1) {
                this.skuNum = 1;
            } else {
                // 
                this.skuNum = parseInt(value);
            }
        },
        // 
        addShopcart() {
            // id
            // this.$store.dispatch("detail/getAddorUpdateShopcartInfo",{skuId:this.$route.params.skuId, skuNum:this.skuNum})
            // storegetAddorUpdateShopcartInfoasyncpromise
            // thencatch
            this.$store
                .dispatch("detail/getAddorUpdateShopcartInfo", {
                    skuId: this.$route.params.skuId,
                    skuNum: this.skuNum,
                })
                .then(
                    // 
                    () => {
                        // ()
                        sessionStorage.setItem("skuInfo", JSON.stringify(this.skuInfo));
                        this.$router.push({
                            name: "addcartsuccess",
                            query: {
                                // 
                                skuNum: this.skuNum,
                                //skuInfoObject
                                // skuInfo: this.skuInfo,
                            },
                        });
                    }
                )
                .catch(
                    // 
                    (error) => {
                        alert(error.message);
                    }
                );
        },
    },
    // detaildiction
    mounted() {
        // console.log(this.$route.params); //{skuId: '2696'}
        this.$store.dispatch("detail/getGoodsInfo", this.$route.params.skuId);
    },
};
</script>

